export const {
  $DOC,
  $WIN,
  $HTMLBODY,
  $HTML,
  $BODY,

  IS_ACTIVE,
  IS_OPEN,
  IS_VISIBLE,
  HAS_FOCUS,
  IS_ANIMATE,
  IS_HIDDEN,
  NO_SCROLL,
  NO_TOUCH,
  HAS_ERROR,
  HAS_TEXT,
  IS_VALID,
  IS_FIXED
} = {
  $DOC: $(document),
  $WIN: $(window),
  $HTMLBODY: $('html, body'),
  $HTML: $('html'),
  $BODY: $('body'),

  IS_ACTIVE: 'is-active',
  IS_OPEN: 'is-open',
  IS_VISIBLE: 'is-visible',
  HAS_FOCUS: 'has-focus',
  IS_ANIMATE: 'is-animate',
  IS_HIDDEN: 'is-hidden',
  NO_SCROLL: 'no-scroll',
  NO_TOUCH: 'no-touch',
  HAS_ERROR: 'has-error',
  HAS_TEXT: 'has-text',
  IS_VALID: 'is-valid',
  IS_FIXED: 'is-fixed'
};
